import { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import NavBar from "../layouts/NavBar";
import AppFooter from "../layouts/AppFooter";
import CardAdmin from "../layouts/CardAdmin";
import Article from "./crud/Article";
import Domaine from "./crud/Domaine";
import FetchHelper from "../helpers/FetchHelper";

export default function Acceuil() {

    const {url}=useParams();
    const [afficheCard,setAffiche]=useState(true);
    const [page,setPage]=useState(null);

    

    const navigate=useNavigate();

    useEffect(()=>{
        const verificationSession=()=>{
            const token=localStorage.getItem("token");
            if(token==null){
                navigate("/login");
            }
            else{
                const fetch=FetchHelper.getData("back/verifiSession");
                fetch.then(response=>{
                    if(response.code!=null){
                        navigate("/login");
                    }
                })
            }
        }
        verificationSession();
        const getUrl=()=>{
            var lien=url.split("-");
            if(lien[0]==="crud"){
                if(lien[1]==="article"){
                    setAffiche(false);
                    document.title="Article";
                    setPage(<Article/>);
                }
                else if(lien[1]==="domaine"){
                    setAffiche(false);
                    document.title="Domaine";
                    setPage(<Domaine/>);
                }
            }
        }
        if(url){
            getUrl();
        }
        else{
            setAffiche(true);
            document.title="Acceuil";
        }
    },[url,navigate])
    return(
        <div>
            <NavBar/>
            {afficheCard ?(
                <div>
                    <CardAdmin/>
                </div>
            ):(
                <div>
                    {page}
                </div>
            )}
            <AppFooter/>
        </div>
    );
}